import React from 'react';
import ModalCont from '../modal/ContainerModal';
import Product from './product/product';
import style from './products.module.css'

const Products = (props) => {
   let product = props.products.map((e, c) => <Product key={c} size={e.size} userId={e.id} id={c} name={e.name} info={e.info} count={e.count} imageUrl={e.imageUrl} products={props.products} />)
   return (
      <div className={style.products}>
         <div className="container">
            <h2 className={style.title}>
               Trending Product
         </h2>
            <div className={style.menu}>
               <div className={style.sort}>
                  Sort:
                  <div>
                     <button onClick={props.setSort('name')} className={style.sorting}>
                        by name
                     </button>
                     <button onClick={props.setSort('count')} className={style.sorting}>
                        by count
                     </button>
                  </div>
               </div>

               <div className={style.btns}>
                  <button onClick={() => {
                     props.setIsOpen(true, 'delete')
                  }} className={style.delete}>
                     delete product
               </button>
                  <button onClick={() => {
                     props.setIsOpen(true, 'create')
                  }} className={style.creat}>
                     Create new product
               </button>
               </div>

            </div>
            <div className={style.items}>
               {product}

            </div>
         </div>
         {props.modal.isOpen ? <ModalCont /> : false}
      </div>
   )
}


export default Products;

