import React from 'react';
import style from './modal.module.css'

const DeleteModal = (props) => {

   return (
      <div className={style.delete}>
         <div className={style.title}>Select what you want to delete:</div>
         <select onChange={(e) => {
            props.changeItem(e.target.value)
         }}>
            <option value="null">choose a product</option>
            {props.products.map((e, c) => <option key={c} value={e.name}>{e.name}</option>)}
         </select>
         {props.modal.deleteValue != null ? <button className={style.btn_delete} onClick={props.deleteItem(props.modal.deleteValue)}>Delete item</button> : false}
      </div>

   )
}





export default DeleteModal;

