import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { addComment, changeCreateColor, changeCreateCount, changeCreateDesc, changeCreateName, changeCreateWeight, changePostComment, setIsOpen } from '../reducers/reducerProducts';
import Details from './details';

class ContainerDetails extends React.Component {

   render() {

      let product = this.props.products.filter(e => e.id == this.props.match.params.userId)[0];

      return (
         <Details modal={this.props.modal}
            setIsOpen={this.props.setIsOpen}
            currentProduct={product}
            products={this.props.products}
            addComment={this.props.addComment}
            currentComment={this.props.currentComment}
            changePostComment={this.props.changePostComment}
            changeCreateCount={this.props.changeCreateCount}
            changeCreateName={this.props.changeCreateName}
            changeCreateColor={this.props.changeCreateColor}
            changeCreateWeight={this.props.changeCreateWeight}
            changeCreateDesc={this.props.changeCreateDesc}
         />
      )
   }
}

function mapStateToProps(state) {

   return {
      products: state.products.products,
      currentComment: state.products.comment,
      modal: state.products.modal

   }
}

let withUrlDataContainer = withRouter(ContainerDetails)
const DetailCont = connect(mapStateToProps, {
   changePostComment,
   addComment,
   setIsOpen,
   changeCreateCount,
   changeCreateName,
   changeCreateDesc,
   changeCreateColor,
   changeCreateWeight
})(withUrlDataContainer);
export default DetailCont;

