import React from 'react';
import style from './modal.module.css'

const CreateModal = (props) => {

   return (
      <div className={style.create}>
         <div className={style.title_create}>
            Create new product
         </div>
         <form  >
            <div className={style.form}>
               <label >Name product:<input value={props.modal.changeCreateName} onChange={e => {
                  props.changeCreateName(e.target.value)
               }} type="text" /></label>
               <label >Description product:<input value={props.modal.changeCreateDesc} onChange={e => {
                  props.changeCreateDesc(e.target.value)
               }} type="text" /></label>
               <label >count product:<input value={props.modal.changeCreateCount} onChange={e => {
                  props.changeCreateCount(e.target.value)
               }} type="number" /></label>
            </div>
            <button type='button' onClick={() => {
               props.createItem()
            }} className={style.btn_create}>Create</button>
            <button type='button' onClick={() => { props.setIsOpen(false) }} className={style.btn_delete}>Cancel</button>
         </form>
      </div>

   )
}



export default CreateModal;

