

import React from 'react';
import { connect } from 'react-redux';
import { addItem, changeCreateColor, changeCreateCount, changeCreateDesc, changeCreateName, changeCreateWeight, changeItem, editProduct, setIsOpen, setSort } from '../reducers/reducerProducts';
import Modal from './modal';

class ContainerModal extends React.Component {

   deleteItem(name) {
      return () => {
         let newItems = this.props.products.filter(e => e.name !== name);
         let result = window.confirm('do you really want to delete the product?')
         if (result) { this.props.setSort(newItems); this.props.setIsOpen(false); console.log(this.props.products); }
         else return;
      }
   }

   createItem() {
      let nweId = 0
      this.props.products.forEach(e => {
         nweId = Math.max(e.id, nweId)
      });

      let newItem = { ...this.props.products[this.props.products.length - 1] };
      newItem.name = this.props.modal.createNameValue;
      newItem.count = this.props.modal.createCountValue;
      newItem.info = this.props.modal.createDescrValue;
      newItem.comments = [];
      newItem.id = nweId + 1;

      if (!(this.props.modal.createNameValue && this.props.modal.createCountValue && this.props.modal.createDescrValue)) alert('fill in all fields');

      else if (window.confirm('do you really want to create the new product?')) {
         this.props.addItem(newItem); this.props.setIsOpen(false)
      }
      else return;
   }

   render() {
      return (
         <Modal currentProduct={this.props.currentProduct}
            createItem={this.createItem.bind(this)}
            changeCreateName={this.props.changeCreateName}
            changeCreateDesc={this.props.changeCreateDesc}
            changeCreateCount={this.props.changeCreateCount}
            deleteItem={this.deleteItem.bind(this)}
            changeItem={this.props.changeItem}
            products={this.props.products}
            modal={this.props.modal}
            setIsOpen={this.props.setIsOpen}
            changeCreateColor={this.props.changeCreateColor}
            changeCreateWeight={this.props.changeCreateWeight}
            editProduct={this.props.editProduct} />
      )
   }
}

function mapStateToProps(state) {
   return {
      modal: state.products.modal,
      products: state.products.products

   }
}


const ModalCont = connect(mapStateToProps, {
   setIsOpen,
   changeItem,
   setSort,
   changeCreateName,
   changeCreateDesc,
   changeCreateCount,
   addItem,
   changeCreateColor,
   changeCreateWeight,
   editProduct
})(ContainerModal);
export default ModalCont;


