import React from 'react';
import ModalCont from '../modal/ContainerModal';
import style from './details.module.css'

const Details = (props) => {
   console.log(props);
   return (
      <div>
         <div className="container">
            <div className={style.detail}>
               <div className={style.img}>
                  <img src={props.currentProduct.imageUrl} alt="product" />
               </div>
               <div className={style.info}>
                  <div className={style.name}>
                     {props.currentProduct.name}
                  </div>
                  <div className={style.desc}>
                     {props.currentProduct.info}
                  </div>
                  <div className={style.proper}>
                     <div className={style.count}>
                        Count: {props.currentProduct.count}
                     </div>
                     <div className={style.color}>
                        color: {props.currentProduct.color}
                     </div>
                     <div className={style.weight}>
                        weight: {props.currentProduct.weight}
                     </div>
                  </div>
                  <div className={style.edit}><button onClick={() => {
                     props.setIsOpen(true, 'edit')
                     props.changeCreateColor(props.currentProduct.color)
                     props.changeCreateCount(props.currentProduct.count)
                     props.changeCreateDesc(props.currentProduct.info)
                     props.changeCreateName(props.currentProduct.name)
                     props.changeCreateWeight(props.currentProduct.weight)
                  }}>Edit product</button></div>
               </div>
            </div>
            <div className={style.add_comment}>
               <textarea value={props.currentComment} onChange={e => {
                  props.changePostComment(e.target.value)
               }} cols="50" rows="5" placeholder="Add comment" />
               <div className={style.btn_comment}>
                  <button onClick={() => {
                     if (props.currentComment === '') return;
                     props.addComment(props.currentProduct.id)
                  }}>Add comment</button>
               </div>
            </div>
            <div className={style.comments}>
               {props.currentProduct.comments.map(e => <p key={e} className={style.text}>{e}</p>)}
            </div>
         </div>
         {props.modal.isOpen ? <ModalCont currentProduct={props.currentProduct} /> : false}
      </div>

   )
}

export default Details;

