

import React from 'react';
import CreateModal from './CreateModal';
import DeleteModal from './deleteModal';
import EditModal from './editModal';
import style from './modal.module.css'

const Modal = (props) => {

   let modal;
   if (props.modal.modalType === 'delete') {
      modal = <DeleteModal deleteItem={props.deleteItem}
         modal={props.modal} changeItem={props.changeItem}
         products={props.products} />
   } else if (props.modal.modalType === 'create') {
      modal = <CreateModal setIsOpen={props.setIsOpen}
         createItem={props.createItem} modal={props.modal}
         changeCreateCount={props.changeCreateCount}
         changeCreateName={props.changeCreateName}
         changeCreateDesc={props.changeCreateDesc}
         products={props.products} />
   } else modal = <EditModal modal={props.modal}
      currentProduct={props.currentProduct}
      changeCreateCount={props.changeCreateCount}
      changeCreateName={props.changeCreateName}
      changeCreateDesc={props.changeCreateDesc}
      changeCreateColor={props.changeCreateColor}
      changeCreateWeight={props.changeCreateWeight}
      editProduct={props.editProduct}
      setIsOpen={props.setIsOpen}
      products={props.products} />
   return (

      <div className={style.modal} onClick={() => {
         props.setIsOpen(false)
      }}>
         <div onClick={(e) => {
            e.stopPropagation()
         }} className={style.content}>
            {modal}
         </div>
      </div>
   )
}


export default Modal;


