import React from 'react';
import style from './modal.module.css'

const EditModal = (props) => {
   let letter;
   [...props.modal.createWeightValue].forEach(e => {
      if (e === 'g') letter = ''
      else letter = 'g';
   });

   return (
      <div className={style.edit}>
         <div className={style.edit_title}>
            Edit the product
         </div>
         <div className={style.name_edit}>
            name: {props.modal.createNameValue}
         </div>
         <input value={props.modal.createNameValue} onChange={e => {
            props.changeCreateName(e.target.value)
         }} type="text" />
         <div className={style.descr_edit}>
            description: {props.modal.createDescrValue}
         </div>
         <input value={props.modal.createDescrValue} onChange={e => {
            props.changeCreateDesc(e.target.value)
         }} type="text" />
         <div className={style.count_edit}>
            count: { }
         </div>
         <input value={props.modal.createCountValue} onChange={e => {
            props.changeCreateCount(e.target.value)
         }} type="number" />
         <div className={style.color_edit}>
            color: {props.modal.createColorValue}
         </div>
         <input value={props.modal.createColorValue} onChange={e => {
            props.changeCreateColor(e.target.value)
         }} type="text" />
         <div className={style.weight_edit}>
            weight: {props.modal.createWeightValue + letter}
         </div>
         <input onChange={e => {
            props.changeCreateWeight(e.target.value + letter)
         }} type="text" />

         <div className={style.btns}>
            <button className={style.edit_btn} onClick={() => {
               props.editProduct(props.currentProduct.id)
               props.setIsOpen(false);
            }}>Edit the product</button>
            <button className={style.cancel_btn} onClick={() => props.setIsOpen(false)}>cancel</button>
         </div>
      </div>

   )
}


export default EditModal;
