
import { Route } from 'react-router';
import { NavLink } from 'react-router-dom';
import './App.css';
import DetailCont from './details/containerDetails';
import Header from './header/header';
import HeaderContent from './headerContent/headerContent';


import ProductCont from './products/containerProducts';





function App() {


   return (
      <div className="App">
         <Header />
         <main>
            <HeaderContent />
            <Route exact path='/' component={() => <ProductCont />} />
            <Route path='/detail/:userId' component={() => <DetailCont />} />
         </main>
         <NavLink to="/" className="main_page">Main page</NavLink>
      </div>
   );
}

export default App;
