import React from 'react';
import { connect } from 'react-redux';
import { setIsOpen, setSort } from '../reducers/reducerProducts';
import Products from './products';


class ContainerProducts extends React.Component {

   setSort(option) {
      return () => {
         let sortByCount = this.props.product.products.sort((prev, next) => {
            if (option === 'count') return prev.count - next.count
            else if (option === 'name') {
               if (prev.name < next.name) return -1;
            }
            return false;
         })
         this.props.setSort(sortByCount)
      }
   }
   setCurrentProduct(id) {
      let currentProduct = this.props.product.products.filter(e => e.id === id)
      this.props.setCurrentProduct(...currentProduct);
   }

   render() {

      return (
         <Products modal={this.props.modal} setIsOpen={this.props.setIsOpen} products={this.props.product.products} setSort={this.setSort.bind(this)} />
      )
   }
}


function mapStateToProps(state) {

   return {
      product: state.products,
      modal: state.products.modal,
   }
}

const ProductCont = connect(mapStateToProps, { setSort, setIsOpen })(ContainerProducts);
export default ProductCont;

